@charset "UTF-8";
.about__wrapper {
  text-align: start;
  padding-top: 70px;
  padding-bottom: 70px;
  position: relative;
}

.about {
  background: #000;
  color: #ffffff;
  overflow: hidden;
  position: relative;
}

video-background {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.video-background video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translate(-50%, -50%);
}

.video-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  z-index: 1;
}

@media (max-width: 767px) {
  .video-background video {
    height: auto;
  }
}
body {
  font-family: "Bebas Neue Bold", sans-serif;
  font-size: 23px;
  line-height: 31px;
  margin: 0;
  background-color: rgb(255, 64, 0);
}

.back-to-top {
  display: none;
  position: fixed;
  bottom: 50%;
  right: 20px;
  z-index: 99;
  font-size: 16px;
  border: none;
  outline: none;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;
  opacity: 0.7;
}

.back-to-top.show {
  display: block;
}

.modal {
  display: none;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal .contacts {
  border: 0;
}

.modal-content {
  position: relative;
  top: 15%;
  background-color: #fefefe;
  margin: auto;
  padding: 10px;
  border: 1px solid #888;
  width: 80%;
  max-width: 800px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.running-text {
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: black;
  color: white;
}

.running-text p {
  display: inline-block;
  animation: running-text 20s linear infinite normal;
  margin: 0;
}

@keyframes running-text {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
