.slider-container {
  position: relative;
  max-width: 100%;
  overflow: hidden;
}

.slider {
  position: relative;
  width: 100%;
  height: 100%;
}

.slider-track {
  display: flex;
  width: 100%;
  height: 400px;
  transition: transform 0.5s ease;
}

.slider-slide {
  flex-shrink: 0;
  width: 100%;
}

.slider-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider-nav {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.slider-dot {
  width: 12px;
  height: 12px;
  margin: 0 6px;
  border-radius: 50%;
  background-color: #ccc;
  cursor: pointer;
  transition: background-color 0.5s ease;
}

.slider-dot.active {
  background-color: #fff;
}

/* Анимация слайдера */
.slider-slide {
  opacity: 0;
  transition: all 0.5s ease-in-out;
}

.slider-slide.active {
  opacity: 1;
}

.intro-category {
